@import '../../styles/main.css';

.contact {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  min-width: 240px;
  background-color: #fff;
  height: 70px;
  padding: 12px;
  width: 100%;
  border-bottom: 1px solid #bdbdbd;
  box-sizing: border-box;
}

.contact:hover {
  background-color: #e0e1e5;
}

.center {
  display: flex;
  flex-direction: column;
  margin-left: 8px;
}

.user {
  display: block;
  font-size: 16px;
  font-weight: 600;
}

.message {
  display: block;
  font-size: 16px;
}

.conteiner {
  display: flex;
  flex-direction: row;
}

.end {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 16px;
}

.unread {
  display: flex;
  flex-direction: row;
  justify-content: center;
  border-radius: 50%;
  font-size: 14px;
  background-color: var(--blue);
  color: #fff;
  text-align: center;
  padding: 2px;
  min-width: 16px;
  height: 16px;
}
